import React, { useCallback, useMemo } from 'react';
import { useEntity } from '../../hooks';
import { PartidoService, type Partido } from '../../../features/partidos/services/partidoService';
import { formatDate, formatDateTime } from '../../../shared/utils copy/formatDate';

interface DetallePartidoProps {
  partidoId: string;
}

interface SetData {
  _id: string;
  numeroSet?: number;
  ganadorSet?: 'local' | 'visitante' | 'empate' | 'pendiente';
  estadoSet?: 'en_juego' | 'finalizado';
  timerSetValue?: number;
  createdAt?: string;
  updatedAt?: string;
}

interface JugadorPartido {
  _id: string;
  teamColor?: 'rojo' | 'azul';
  preRating?: number;
  postRating?: number;
  delta?: number;
  playerId: {
    _id: string;
    nombre: string;
    alias?: string;
  };
}

type PartidoDetalle = Partido & {
  sets?: SetData[];
  jugadores?: JugadorPartido[];
  rankedMeta?: {
    teamColors?: {
      local?: 'rojo' | 'azul';
      visitante?: 'rojo' | 'azul';
    };
    snapshot?: {
      teamAverages?: {
        rojo?: number;
        azul?: number;
      };
    };
  };
  updatedAt?: string;
  creadoPor?: string;
};

const estadoStyles: Record<string, { label: string; badgeClass: string }> = {
  programado: { label: 'Programado', badgeClass: 'bg-slate-100 text-slate-700 border border-slate-200' },
  en_juego: { label: 'En juego', badgeClass: 'bg-amber-100 text-amber-700 border border-amber-200 animate-pulse' },
  finalizado: { label: 'Finalizado', badgeClass: 'bg-emerald-100 text-emerald-700 border border-emerald-200' },
  cancelado: { label: 'Cancelado', badgeClass: 'bg-red-100 text-red-600 border border-red-200' },
};

const formatRating = (value?: number | null) => {
  if (value === null || value === undefined) return 'N/A';
  return value.toFixed(3);
};

const formatDelta = (value?: number | null) => {
  if (value === null || value === undefined) return 'N/A';
  const sign = value > 0 ? '+' : '';
  return `${sign}${value.toFixed(3)}`;
};

const DetallePartido: React.FC<DetallePartidoProps> = ({ partidoId }) => {
  const fetchPartido = useCallback(() => PartidoService.getById(partidoId), [partidoId]);
  const { data: partido, loading, error } = useEntity<PartidoDetalle>(fetchPartido);

  const sortedSets = useMemo(() => {
    const sets = partido?.sets ?? [];
    return sets.slice().sort((a, b) => (a.numeroSet ?? 0) - (b.numeroSet ?? 0));
  }, [partido?.sets]);

  const teamColors = useMemo<{ local: 'rojo' | 'azul'; visitante: 'rojo' | 'azul' }>(
    () => ({
      local: partido?.rankedMeta?.teamColors?.local || 'rojo',
      visitante: partido?.rankedMeta?.teamColors?.visitante || 'azul',
    }),
    [partido?.rankedMeta?.teamColors?.local, partido?.rankedMeta?.teamColors?.visitante]
  );

  const jugadoresAgrupados = useMemo(() => {
    const grupos = {
      local: [] as JugadorPartido[],
      visitante: [] as JugadorPartido[],
      otros: [] as JugadorPartido[],
    };

    const lista = partido?.jugadores ?? [];
    lista.forEach((jugador) => {
      if (jugador.teamColor === teamColors.local) {
        grupos.local.push(jugador);
      } else if (jugador.teamColor === teamColors.visitante) {
        grupos.visitante.push(jugador);
      } else {
        grupos.otros.push(jugador);
      }
    });

    return {
      ...grupos,
      total: lista.length,
    };
  }, [partido?.jugadores, teamColors.local, teamColors.visitante]);

  const marcadorLocal = partido?.marcadorLocal ?? 0;
  const marcadorVisitante = partido?.marcadorVisitante ?? 0;
  const empate = marcadorLocal === marcadorVisitante;
  const localEnVentaja = marcadorLocal > marcadorVisitante;
  const visitanteEnVentaja = marcadorVisitante > marcadorLocal;

  const promediosRanked = partido?.rankedMeta?.snapshot?.teamAverages;

  const ganadorTexto = useMemo(() => {
    if (!partido) return 'N/A';
    if (empate) return 'Empate';
    if (localEnVentaja) return partido.equipoLocal?.nombre || 'Equipo local';
    if (visitanteEnVentaja) return partido.equipoVisitante?.nombre || 'Equipo visitante';
    return 'N/A';
  }, [empate, localEnVentaja, partido, visitanteEnVentaja]);

  const ganadorDisponible = ganadorTexto !== 'N/A';

  const estado = partido?.estado || 'programado';
  const estadoInfo = estadoStyles[estado] || estadoStyles.programado;

  if (loading) {
    return (
      <div className="flex min-h-[420px] items-center justify-center">
        <div className="text-center">
          <div className="mb-4 h-10 w-10 animate-spin rounded-full border-4 border-brand-200 border-t-brand-600 mx-auto" />
          <p className="text-slate-600">Cargando detalles del partido...</p>
        </div>
      </div>
    );
  }

  if (error || !partido) {
    return (
      <div className="flex min-h-[420px] items-center justify-center">
        <div className="text-center">
          <p className="mb-4 text-red-600">No pudimos cargar el partido: {error || 'No disponible'}</p>
        </div>
      </div>
    );
  }

  const fechaTexto = partido.fecha && partido.hora
    ? formatDateTime(`${partido.fecha}T${partido.hora}`)
    : partido.fecha
    ? formatDate(partido.fecha)
    : 'Fecha no disponible';

  return (
    <div className="mx-auto flex w-full max-w-5xl flex-col gap-8">
      <section className="relative overflow-hidden rounded-3xl border border-slate-200 bg-white shadow-xl">
        <div className="absolute inset-x-0 top-0 h-32 bg-gradient-to-r from-brand-500/15 via-brand-600/20 to-brand-500/15" />
        <div className="relative p-8 sm:p-10">
          <div className="flex flex-col gap-6 lg:flex-row lg:items-start lg:justify-between">
            <div className="space-y-3">
              <span className={`inline-flex items-center gap-2 rounded-full px-4 py-1 text-xs font-semibold uppercase tracking-wide ${estadoInfo.badgeClass}`}>
                {estadoInfo.label}
              </span>
              <h1 className="text-2xl font-bold tracking-tight text-slate-900 sm:text-3xl">
                {partido.nombre || `${partido.equipoLocal?.nombre || 'Local'} vs ${partido.equipoVisitante?.nombre || 'Visitante'}`}
              </h1>
              <p className="text-sm text-slate-500">{fechaTexto}</p>
              {ganadorDisponible && (
                <p className="inline-flex items-center gap-2 rounded-full bg-emerald-50 px-3 py-1 text-xs font-semibold text-emerald-700">
                  {estado === 'finalizado' ? 'Ganador' : 'Parcial'}: {ganadorTexto}
                </p>
              )}
            </div>

            <div className="rounded-2xl border border-white/60 bg-gradient-to-br from-slate-900 via-slate-800 to-slate-900 px-6 py-5 text-white shadow-lg">
              <div className="flex items-center justify-between gap-6">
                <div className={`flex flex-col items-center gap-2 text-center ${localEnVentaja ? 'text-emerald-300' : 'text-white/80'}`}>
                  <span className="text-xs uppercase tracking-wide text-white/60">{partido.equipoLocal?.nombre || 'Local'}</span>
                  <span className="text-4xl font-black leading-none">{marcadorLocal}</span>
                </div>
                <div className="flex flex-col items-center text-sm font-semibold text-white/60">
                  <span className="text-lg">VS</span>
                  <span className="text-[10px] uppercase tracking-[0.4em]">Marcador</span>
                </div>
                <div className={`flex flex-col items-center gap-2 text-center ${visitanteEnVentaja ? 'text-emerald-300' : 'text-white/80'}`}>
                  <span className="text-xs uppercase tracking-wide text-white/60">{partido.equipoVisitante?.nombre || 'Visitante'}</span>
                  <span className="text-4xl font-black leading-none">{marcadorVisitante}</span>
                </div>
              </div>
            </div>
          </div>

          <div className="mt-8 grid gap-6 sm:grid-cols-2 lg:grid-cols-4">
            <div className="rounded-2xl border border-slate-100 bg-slate-50/80 p-4">
              <p className="text-xs font-semibold uppercase tracking-wide text-slate-500">Competencia</p>
              <p className="mt-1 text-sm font-medium text-slate-900">{partido.competencia?.nombre || 'N/A'}</p>
            </div>
            <div className="rounded-2xl border border-slate-100 bg-slate-50/80 p-4">
              <p className="text-xs font-semibold uppercase tracking-wide text-slate-500">Fase</p>
              <p className="mt-1 text-sm font-medium text-slate-900">{partido.fase?.nombre || 'N/A'}</p>
            </div>
            <div className="rounded-2xl border border-slate-100 bg-slate-50/80 p-4">
              <p className="text-xs font-semibold uppercase tracking-wide text-slate-500">Escenario</p>
              <p className="mt-1 text-sm font-medium text-slate-900">{partido.escenario || 'N/A'}</p>
            </div>
            <div className="rounded-2xl border border-slate-100 bg-slate-50/80 p-4">
              <p className="text-xs font-semibold uppercase tracking-wide text-slate-500">Modalidad</p>
              <p className="mt-1 text-sm font-medium text-slate-900">{(partido as any).modalidad || 'N/A'}</p>
            </div>
          </div>
        </div>
      </section>

      <section className="rounded-3xl border border-slate-200 bg-white p-6 shadow-lg sm:p-8">
        <header className="mb-6 flex flex-col gap-2 sm:flex-row sm:items-center sm:justify-between">
          <div>
            <h2 className="text-lg font-semibold text-slate-900">Resultados por set</h2>
            <p className="text-sm text-slate-500">Detalle del progreso del partido set a set.</p>
          </div>
          <span className="rounded-full bg-slate-100 px-3 py-1 text-xs font-medium text-slate-500">
            {sortedSets.length} {sortedSets.length === 1 ? 'set' : 'sets'}
          </span>
        </header>

        {sortedSets.length === 0 ? (
          <div className="rounded-2xl border border-dashed border-slate-200 bg-slate-50 p-6 text-center text-sm text-slate-500">
            Aún no hay sets cargados para este partido.
          </div>
        ) : (
          <div className="grid gap-4 sm:grid-cols-2">
            {sortedSets.map((set) => {
              const ganadorNombre = set.ganadorSet === 'local'
                ? partido.equipoLocal?.nombre || 'Local'
                : set.ganadorSet === 'visitante'
                ? partido.equipoVisitante?.nombre || 'Visitante'
                : set.ganadorSet === 'empate'
                ? 'Empate'
                : 'Pendiente';

              const ganadorClass = set.ganadorSet === 'local'
                ? 'border border-brand-200 bg-brand-50 text-brand-700'
                : set.ganadorSet === 'visitante'
                ? 'border border-sky-200 bg-sky-50 text-sky-700'
                : set.ganadorSet === 'empate'
                ? 'border border-amber-200 bg-amber-50 text-amber-700'
                : 'border border-slate-200 bg-white text-slate-600';

              return (
                <article
                  key={set._id}
                  className="relative overflow-hidden rounded-2xl border border-slate-200 bg-white p-5 shadow-sm transition hover:-translate-y-0.5 hover:shadow-md"
                >
                  <div className="flex items-center justify-between">
                    <div>
                      <p className="text-xs font-semibold uppercase tracking-wide text-slate-400">Set</p>
                      <p className="text-xl font-bold text-slate-900">{set.numeroSet ?? 'N/A'}</p>
                    </div>
                    <span className={`inline-flex items-center rounded-full px-3 py-1 text-[11px] font-semibold ${
                      set.estadoSet === 'finalizado'
                        ? 'bg-emerald-100 text-emerald-700'
                        : 'bg-amber-100 text-amber-700'
                    }`}>
                      {set.estadoSet === 'finalizado' ? 'Finalizado' : 'En juego'}
                    </span>
                  </div>
                  <div className="mt-4 rounded-xl border border-dashed border-slate-200 bg-slate-50 p-4">
                    <p className="text-xs font-semibold uppercase tracking-wide text-slate-400">Ganador</p>
                    <p className={`mt-2 inline-flex items-center rounded-lg px-3 py-2 text-sm font-semibold ${ganadorClass}`}>
                      {ganadorNombre}
                    </p>
                  </div>
                </article>
              );
            })}
          </div>
        )}
      </section>

      <section className="rounded-3xl border border-slate-200 bg-white p-6 shadow-lg sm:p-8">
        <header className="mb-6 flex flex-col gap-2 sm:flex-row sm:items-center sm:justify-between">
          <div>
            <h2 className="text-lg font-semibold text-slate-900">Jugadores destacados</h2>
            <p className="text-sm text-slate-500">Participantes asociados al partido y su variación de rating.</p>
          </div>
          <span className="rounded-full bg-slate-100 px-3 py-1 text-xs font-medium text-slate-500">
            {jugadoresAgrupados.total} {jugadoresAgrupados.total === 1 ? 'jugador' : 'jugadores'}
          </span>
        </header>

        {jugadoresAgrupados.total === 0 ? (
          <div className="rounded-2xl border border-dashed border-slate-200 bg-slate-50 p-6 text-center text-sm text-slate-500">
            No hay jugadores asociados a este partido todavía.
          </div>
        ) : (
          <div className="grid gap-6 lg:grid-cols-2">
            <div className="rounded-2xl border border-slate-200 bg-gradient-to-b from-slate-50 to-white p-5">
              <header className="mb-4 flex items-center justify-between">
                <div>
                  <p className="text-xs font-semibold uppercase tracking-wide text-slate-500">Equipo local</p>
                  <p className="text-sm font-medium text-slate-900">{partido.equipoLocal?.nombre || 'Local'}</p>
                </div>
                <span className="inline-flex items-center gap-2 rounded-full bg-slate-900/90 px-3 py-1 text-[11px] font-semibold uppercase tracking-wide text-white">
                  {teamColors.local === 'rojo' ? 'Color rojo' : 'Color azul'}
                </span>
              </header>
              <div className="space-y-3">
                {jugadoresAgrupados.local.length === 0 ? (
                  <p className="text-sm text-slate-500">Sin registros para este lado.</p>
                ) : (
                  jugadoresAgrupados.local.map((jugador) => (
                    <div key={jugador._id} className="rounded-xl border border-slate-200 bg-white px-4 py-3 shadow-sm">
                      <div className="flex items-center justify-between">
                        <div>
                          <p className="text-sm font-semibold text-slate-900">{jugador.playerId.nombre}</p>
                          {jugador.playerId.alias && (
                            <p className="text-xs text-slate-500">Alias: {jugador.playerId.alias}</p>
                          )}
                        </div>
                        <div className="text-right text-[11px] font-semibold uppercase tracking-wide text-slate-400">
                          {formatDelta(jugador.delta)}
                        </div>
                      </div>
                      <dl className="mt-3 grid grid-cols-2 gap-3 text-xs">
                        <div className="rounded-lg bg-slate-50 p-2">
                          <dt className="text-[10px] font-semibold uppercase text-slate-400">Pre</dt>
                          <dd className="mt-1 font-medium text-slate-700">{formatRating(jugador.preRating)}</dd>
                        </div>
                        <div className="rounded-lg bg-slate-50 p-2">
                          <dt className="text-[10px] font-semibold uppercase text-slate-400">Post</dt>
                          <dd className="mt-1 font-medium text-slate-700">{formatRating(jugador.postRating)}</dd>
                        </div>
                      </dl>
                    </div>
                  ))
                )}
              </div>
            </div>

            <div className="rounded-2xl border border-slate-200 bg-gradient-to-b from-slate-50 to-white p-5">
              <header className="mb-4 flex items-center justify-between">
                <div>
                  <p className="text-xs font-semibold uppercase tracking-wide text-slate-500">Equipo visitante</p>
                  <p className="text-sm font-medium text-slate-900">{partido.equipoVisitante?.nombre || 'Visitante'}</p>
                </div>
                <span className="inline-flex items-center gap-2 rounded-full bg-slate-900/90 px-3 py-1 text-[11px] font-semibold uppercase tracking-wide text-white">
                  {teamColors.visitante === 'azul' ? 'Color azul' : 'Color rojo'}
                </span>
              </header>
              <div className="space-y-3">
                {jugadoresAgrupados.visitante.length === 0 ? (
                  <p className="text-sm text-slate-500">Sin registros para este lado.</p>
                ) : (
                  jugadoresAgrupados.visitante.map((jugador) => (
                    <div key={jugador._id} className="rounded-xl border border-slate-200 bg-white px-4 py-3 shadow-sm">
                      <div className="flex items-center justify-between">
                        <div>
                          <p className="text-sm font-semibold text-slate-900">{jugador.playerId.nombre}</p>
                          {jugador.playerId.alias && (
                            <p className="text-xs text-slate-500">Alias: {jugador.playerId.alias}</p>
                          )}
                        </div>
                        <div className="text-right text-[11px] font-semibold uppercase tracking-wide text-slate-400">
                          {formatDelta(jugador.delta)}
                        </div>
                      </div>
                      <dl className="mt-3 grid grid-cols-2 gap-3 text-xs">
                        <div className="rounded-lg bg-slate-50 p-2">
                          <dt className="text-[10px] font-semibold uppercase text-slate-400">Pre</dt>
                          <dd className="mt-1 font-medium text-slate-700">{formatRating(jugador.preRating)}</dd>
                        </div>
                        <div className="rounded-lg bg-slate-50 p-2">
                          <dt className="text-[10px] font-semibold uppercase text-slate-400">Post</dt>
                          <dd className="mt-1 font-medium text-slate-700">{formatRating(jugador.postRating)}</dd>
                        </div>
                      </dl>
                    </div>
                  ))
                )}
              </div>
            </div>

            {jugadoresAgrupados.otros.length > 0 && (
              <div className="lg:col-span-2">
                <div className="rounded-2xl border border-dashed border-slate-200 bg-slate-50 p-5">
                  <p className="text-xs font-semibold uppercase tracking-wide text-slate-500">Jugadores sin color asignado</p>
                  <div className="mt-4 grid gap-3 sm:grid-cols-2">
                    {jugadoresAgrupados.otros.map((jugador) => (
                      <div key={jugador._id} className="rounded-xl border border-slate-200 bg-white px-4 py-3 shadow-sm">
                        <div className="flex items-center justify-between">
                          <div>
                            <p className="text-sm font-semibold text-slate-900">{jugador.playerId.nombre}</p>
                            {jugador.playerId.alias && (
                              <p className="text-xs text-slate-500">Alias: {jugador.playerId.alias}</p>
                            )}
                          </div>
                          <span className="text-[11px] font-semibold uppercase tracking-wide text-slate-400">
                            {jugador.teamColor || 'N/A'}
                          </span>
                        </div>
                        <dl className="mt-3 grid grid-cols-3 gap-3 text-xs">
                          <div className="rounded-lg bg-slate-50 p-2">
                            <dt className="text-[10px] font-semibold uppercase text-slate-400">Pre</dt>
                            <dd className="mt-1 font-medium text-slate-700">{formatRating(jugador.preRating)}</dd>
                          </div>
                          <div className="rounded-lg bg-slate-50 p-2">
                            <dt className="text-[10px] font-semibold uppercase text-slate-400">Post</dt>
                            <dd className="mt-1 font-medium text-slate-700">{formatRating(jugador.postRating)}</dd>
                          </div>
                          <div className="rounded-lg bg-slate-50 p-2">
                            <dt className="text-[10px] font-semibold uppercase text-slate-400">Δ</dt>
                            <dd className="mt-1 font-medium text-slate-700">{formatDelta(jugador.delta)}</dd>
                          </div>
                        </dl>
                      </div>
                    ))}
                  </div>
                </div>
              </div>
            )}
          </div>
        )}
      </section>

      {promediosRanked && (
        <section className="rounded-3xl border border-slate-200 bg-slate-900 p-6 text-white shadow-lg sm:p-8">
          <header className="mb-4">
            <h2 className="text-lg font-semibold">Resumen Ranked</h2>
            <p className="text-sm text-white/70">Promedios de rating calculados para cada color de equipo.</p>
          </header>
          <div className="grid gap-4 sm:grid-cols-2">
            <div className="rounded-2xl border border-white/10 bg-white/5 p-5">
              <p className="text-xs font-semibold uppercase tracking-wide text-white/60">Promedio equipo {teamColors.local}</p>
              <p className="mt-2 text-3xl font-black">{formatRating(promediosRanked[teamColors.local])}</p>
            </div>
            <div className="rounded-2xl border border-white/10 bg-white/5 p-5">
              <p className="text-xs font-semibold uppercase tracking-wide text-white/60">Promedio equipo {teamColors.visitante}</p>
              <p className="mt-2 text-3xl font-black">{formatRating(promediosRanked[teamColors.visitante])}</p>
            </div>
          </div>
        </section>
      )}

      <section className="mb-2 rounded-3xl border border-slate-200 bg-white p-6 shadow-lg sm:p-8">
        <h2 className="text-lg font-semibold text-slate-900">Metadatos del partido</h2>
        <dl className="mt-4 grid grid-cols-1 gap-4 text-sm sm:grid-cols-2">
          <div className="rounded-xl border border-slate-100 bg-slate-50 p-4">
            <dt className="text-xs font-semibold uppercase tracking-wide text-slate-500">Creado por</dt>
            <dd className="mt-1 font-medium text-slate-800">{partido.creadoPor || 'N/A'}</dd>
          </div>
          <div className="rounded-xl border border-slate-100 bg-slate-50 p-4">
            <dt className="text-xs font-semibold uppercase tracking-wide text-slate-500">Estado</dt>
            <dd className="mt-1 font-medium capitalize text-slate-800">{(partido.estado || 'programado').replace('_', ' ')}</dd>
          </div>
          <div className="rounded-xl border border-slate-100 bg-slate-50 p-4">
            <dt className="text-xs font-semibold uppercase tracking-wide text-slate-500">Administradores</dt>
            <dd className="mt-1 font-medium text-slate-800">
              {(Array.isArray((partido as any).administradores) && (partido as any).administradores.length > 0)
                ? (partido as any).administradores.join(', ')
                : 'N/A'}
            </dd>
          </div>
          <div className="rounded-xl border border-slate-100 bg-slate-50 p-4">
            <dt className="text-xs font-semibold uppercase tracking-wide text-slate-500">Última actualización</dt>
            <dd className="mt-1 font-medium text-slate-800">
              {partido.updatedAt ? formatDateTime(partido.updatedAt) : 'N/A'}
            </dd>
          </div>
        </dl>
      </section>
    </div>
  );
};

export default DetallePartido;
